.share-page{
    width: 100vw;
    height: 100vh;
    color: rgb(20, 20, 20);
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.share-page-title-container{
    width: 80%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: rgb(51,51,51);
    border-bottom: 1px solid rgb(51,51,51);
    margin-bottom: 2rem;
}
.share-page-title{
    line-height: 25%;
    font-size: 2.5rem;
}
.chat-bubble-icon{
    width: 3rem;
    padding: 0.5rem;
}
.share-container{
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 80%;
    background-color:#E6E6E6;
    padding: 2.5rem;
    font-weight: 500;
}
.share-info{
    font-size: 1.5rem;
    margin-bottom: 2.25rem;
}
.share-links{
    display: flex;
    align-items: center;
}
.share-url{
    font-size: 1.5rem;
    padding: 0.5rem;
    background-color: white;
    border-radius: 0.25rem;
    flex: 1;
}
.share-link-button{
    width: 3.5rem;
    height: 3.5rem;
    margin-left: 1rem;
    margin-right: 1rem;
    border: none;
    outline: none;
    background: none;
    transition: all ease-in-out 0.2s;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.share-link-button::before{
    content: attr(data-tooltip);
    transform: scale(0);
    height: 1.5rem;
    font-size: 0.75rem;
    padding-top: 0.5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    text-align: center;
    border-radius: 0.25rem;
    color: white;
    background-color: rgb(100, 100, 100);
    position: absolute;
    top: -2.5rem;
    transition: all ease-in-out 0.2s;
    transform-origin: center bottom;
}
.share-link-button::after{
    content: "";
    transform: scale(0);
    width: 0; 
    height: 0;
    position: absolute;
    top: -0.6rem;
    border-left: 0.5rem solid transparent;
    border-right: 0.5rem solid transparent;
    border-top: 0.5rem solid rgb(100, 100, 100);
    transition: all ease-in-out 0.2s;
    transform-origin: center top;
}
.share-link-button:hover::before,.share-link-button:hover::after {
    transform: scale(1);
    display: block;
    transition: all ease-in-out 0.1s;
}

.share-link-button img{
    width: 100%;
}
.share-link-button:hover{
    cursor: pointer;
    transform: translateY(-10%);
    transition: all ease-in-out 0.2s;
    filter: drop-shadow(0.35rem 0.25rem 2px #00000015);
}
.how-it-works{
    margin-top: 4rem;
    width: 80%;
    font-weight: 400;
}
.share-steps-container{
    width: 80%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 1.25rem;
}
.share-step{
    margin: 2rem;
    margin-top: 1rem;
    margin-left: 0;
    background-color:#E6E6E6;
    padding: 1rem;
    border-radius: 0.25rem;
    flex: 1;
    display: flex;
    align-items: center;
}
.share-step img{
    width: 4rem;
    margin: 1rem;
    opacity: 50%;
}
.share-step span{
    font-weight: bold;

}