.App-header{
    position: absolute;
    width: 100%;
    padding: 0.5rem;
    padding-top: 1rem;
    padding-left: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    /*background: linear-gradient(0deg, rgba(0,0,0,0) 65%, rgba(0, 0, 0, 0.7) 100%);*/
    background-color: black;
  }
  .arrow{
    width: 0; 
    height: 0; 
    border-left: 0.35rem solid transparent;
    border-right: 0.35rem solid transparent;
    border-top: 0.35rem solid white;
    margin-left: 0.5rem;
    margin-right: 2vw;
    transition: all ease-in-out 0.2s;
  }
  .arrow:hover{
    cursor: pointer;
    transform: rotate(180deg);
    transition: all ease-in-out 0.1.5s;
  }
  .profile-photo{
    width: 2.5rem;
    margin-left: 1rem;
    border-radius: 999px;
  }
  .bell-icon{
    width: 1.25rem;
    transition: ease-in-out 0.2s;
    box-sizing: border-box;
    margin-left: 1rem;
    margin-right: 1rem;
    position: relative;
    z-index: 4;
  }
  .bell-icon:hover{
    animation-name: bell;
    animation-duration: 0.3s;
    cursor: pointer;
  }
  @keyframes bell{
    0% {transform: rotate(5deg);}
    20% {transform: rotate(-10deg);}
    40% {transform: rotate(10deg);}
    60% {transform: rotate(-10deg);}
    80% {transform: rotate(5deg);}
    100%{}
  }
  .gift-icon{
    width: 1.25rem;
    transition: ease-in-out 0.2s;
    box-sizing: border-box;
    margin-left: 1rem;
    margin-right: 1rem;
    position: relative;
    z-index: 4;
    transform: translateY(-10%);
  }
  .gift-icon:hover{
    cursor: pointer;
    transform: translateY(-20%);
  }
  .notifications{
    width: 15rem;
    padding: 1.5rem;
    position: absolute;
    top: 150%;
    left: -12rem;
    background-color: #00000050;
    border: 1px solid #6161617a;
    border-top: 0.5px solid white;
    z-index: 3;
    text-align: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: opacity ease-in-out 0.2s;
}
.notifications::before{
    width: 0; 
    height: 0; 
    border-left: 0.35rem solid transparent;
    border-right: 0.35rem solid transparent;
    border-top: 0.35rem solid white;
    margin-left: 0.5rem;
    margin-right: 2vw;
    transition: all ease-in-out 0.2s;
}
  .notifications-container{
      position: relative;
  }
.account-menu{
    width: 15rem;
    padding: 1rem;
    position: absolute;
    top: 120%;
    left: -10rem;
    background-color: #00000050;
    border: 1px solid #6161617a;
    border-top: 0.5px solid white;
    z-index: 3;
    text-align: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: opacity ease-in-out 0.2s;
}
.account-container{
    position: relative;
}