:root{
    --empty-color: rgb(70, 70, 70);
}
.flix-page{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.flix-page-title{
    font-size: 4rem;
}
.nominated-flix-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90vw;
    height: 70vh;
    color: var(--empty-color);
    background: rgb(10, 10, 10);
    font-size: 2rem;
    text-align: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.nominated-list{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-position: inside;
    color: var(--empty-color);
}
.empty-flix{
    margin: 1rem;
    flex-basis: 18%;
    height: 25vw;
    border: 2px dashed var(--empty-color);
    text-align: left;
    font-weight: bold;
    font-size: 2rem;

    padding: 10px;

}
